import React from "react";
import styled from "styled-components";
import { graphql } from "gatsby";

import Layout from "../components/layout";
import SEO from "../components/seo";
import Card from "../components/post-card";

export const PostList = styled.ul`
  padding: 0 20px;
  margin: 0 auto;
  ${({ theme }) => theme.breakpointWidthSmall} {
    max-width: 720px;
  }
  ${({ theme }) => theme.breakpointWidthLarge} {
    max-width: 1000px;
  }
`;

const IndexPage = ({ data }) => {
  const as1 = data.allMdx.edges.map(({ node }) => (
    <Card
      key={node.id}
      title={node.frontmatter.title}
      url={node.fields.slug}
      type={node.fields.type}
      desc="作为一名设计师，深入思考一个产品是必要的，只有理解产品的运作机制，才能够把握设计的方向和本质，设计出用户真正需要的功能。"
      image={node.frontmatter.featuredImage}
    ></Card>
  ));
  return (
    <Layout>
      <SEO title="首页 Home" />
      <PostList>{as1}</PostList>
    </Layout>
  );
};

export const query = graphql`
  query {
    allMdx {
      edges {
        node {
          id
          fields {
            slug
            type
          }
          frontmatter {
            title
            featuredImage {
              colors {
                lightMuted
                darkVibrant
                lightVibrant
                vibrant
              }
              childImageSharp {
                sizes(maxWidth: 960) {
                  ...GatsbyImageSharpSizes
                }
              }
            }
          }
        }
      }
    }
  }
`;

export default IndexPage;
