import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"
import Img from "gatsby-image"

const CardContainer = styled.li`
  list-style: none;
  display: block;
  padding: 0;
  margin: 0 0 32px 0;
  background: ${({theme, type, bgColor}) => {
    if (type === 'work')
      return bgColor
    else
      return theme.cardBgColor
  }};
  border-radius: 16px;
  overflow: hidden;

  ${({theme}) => theme.breakpointWidthSmall} {
    margin: 0 0 100px 0;
    border-radius: 24px;
  }
`
const Image = styled(Img)`
  grid-column: image;
  grid-row: image;
  
`
const Title = styled.h2`
  font-size: 1.6rem;
  padding: 0 20px;
  margin-top: 24px;
  margin-bottom: 1rem;

  color: ${({type, bgColor}) => type === 'work' ? '#fff' : '#000'};

  ${({theme}) => theme.breakpointWidthSmall} {
    grid-row: title;
    grid-column: caption;
    padding: 0 48px;
    margin-top: 0;
    margin-bottom: 1.666rem;
  }

  ${({theme}) => theme.breakpointWidthLarge} {
    font-size: 2.5rem;
  }
`

const Description = styled.p`
  font-size: .85rem;
  padding: 0 20px;

  color: ${({type, bgColor}) => type === 'work' ? 'rgba(255,255,255,.7)' : '#000'};

  ${({theme}) => theme.breakpointWidthSmall} {
    grid-row: description;
    grid-column: caption;
    line-height: 1.5;

    padding: 0 48px;
    margin-top: 0;
  }
  
  ${({theme}) => theme.breakpointWidthLarge} {
    font-size: 1rem;
  }
`

const LinkContaienr = styled(Link)`
  text-decoration: none;
  display: block;
  height: auto;

  ${({theme}) => theme.breakpointWidthSmall} {
    display: grid;
    height: 390px;

    grid-template-columns: 
      [caption-start] 1fr
      [caption-end image-start] 1fr
      [image-end];

      grid-template-rows: 
      [margin-start image-start] 64px 
      [margin-end title-start] max-content
      [title-end description-start] auto
      [description-end tag-start] max-content
      [tag-end margin-bottom-start] 32px
      [margin-bottom-end image-end];
  }

  ${({theme}) => theme.breakpointWidthLarge} {
    display: grid;
    height: 550px;

    grid-template-rows: 
      [margin-start image-start] 128px 
      [margin-end title-start] max-content
      [title-end description-start] auto
      [description-end tag-start] max-content
      [tag-end margin-bottom-start] 48px
      [margin-bottom-end image-end];
  }
`

const PostType = styled.div`
  grid-row: tag;
  grid-column: caption;
  padding: 0 20px 24px 20px;
  font-weight: bold;
  color: ${({type, textColor}) => type === 'work' ? textColor : '#000'};

  ${({theme}) => theme.breakpointWidthSmall} {
    padding-left: 48px;
  } 
`

const TYPES = {
  article: '文章',
  work: '项目'
}

const Card = ({ title, desc, image, url, type }) => {
  return (
    <CardContainer type={type} bgColor={image.colors.darkVibrant}>
      <LinkContaienr to={url}>
        <Image sizes={image.childImageSharp.sizes} />
        <Title type={type} bgColor={image.colors.darkVibrant}>{title}</Title>
        <Description type={type} bgColor={image.colors.darkVibrant}>{desc}</Description>
        <PostType type={type} textColor={image.colors.vibrant}>
          { TYPES[type] }
        </PostType>
      </LinkContaienr>
    </CardContainer>
  )
}
export default Card